import React, { Component } from 'react';
import { connect } from 'react-redux';
import { NavBar, Icon, } from 'antd-mobile';
import ReactDOM from 'react-dom';
import Content from '../../views/common/Content';
import { 
    goBackAction, 
    setScrollerPositionAction,
} from '../../store/actions/traffic/listAction';

import '../../assets/css/traffic.css';

class TrafficListScreen extends Component
{
    componentDidMount()
    {
        this.restoreScrollViewPosition();
    }
    render()
    {
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        let dispatch = this.props.dispatch;
                        this.props.history.goBack();
                        dispatch(goBackAction());
                    }}>
                    交通指南
                </NavBar>
                <Content ref={(view)=>{this.scrollView = view;}}>
                    <img src={require('../../assets/images/traffic/list_top.png')} alt='' className="trafficTopImage"/>
                    {
                        this.renderListView()
                    }
                </Content>
            </div>
        );
    }
    renderListView()
    {
        let list = [{
            icon: require('../../assets/images/traffic/air.png'),
            title: '航空',
            pathname: '/traffic/air',
            desc: '机场信息，是一种便利的交通工具。'
        }, {
            icon: require('../../assets/images/traffic/train.png'),
            title: '火车',
            desc: '新县站在位于地处大别山腹地，鄂豫皖三省结合部。',
            pathname: '/traffic/train'
        }, {
            icon: require('../../assets/images/traffic/coach.png'),
            title: '客运',
            desc: '新县汽车站，地处河南，是国家城市枢纽站之一。',
            pathname: '/traffic/bus',
        }, {
            icon: require('../../assets/images/traffic/local.jpg'),
            title: '本地交通',
            desc: '提供新县当地交通的交通工具。',
            pathname: '/traffic/local',
        }];
        return list.map((item, index)=>this.renderItemView(item, index));
    }
    renderItemView(item, index)
    {
        return (
            <div className="trafficListItemView"
                key={index}
                onClick={()=>this.showDetails(item)}>
                <div className="trafficListItemIcon" style={{ backgroundImage: `url(${item.icon})` }}/>
                <div className="trafficListItemRight">
                    <div className="trafficListItemTitle">
                        <img src={require('../../assets/images/traffic/more.png')} alt=''/>
                        { item.title }
                    </div>
                    <div className="trafficListItemDesc">
                        { item.desc }
                    </div>
                </div>
            </div>
        );
    }
    showDetails(item)
    {
        this.cacheScrollOffset();
        if(item && item.pathname && item.pathname.length > 0)
        {
            this.props.history.push(item.pathname);
        }
    }
    cacheScrollOffset()
    {
        let offsetY = ReactDOM.findDOMNode(this.scrollView).scrollTop;
        this.props.dispatch(setScrollerPositionAction(offsetY));
    }
    restoreScrollViewPosition()
    {
        if(this.scrollView)
        {
            if(!isNaN(this.props.scrollViewOffset))
            {
                this.scrollView.scrollToY(this.props.scrollViewOffset);
            }
            else
            {
                this.scrollView.scrollToY(0);
            }
        }
    }
}

const mapStateToProps = function(state, ownProps)
{
    let obj = state.traffic.listScreen;
    return {
        ...obj,
    };
};

TrafficListScreen = connect(mapStateToProps)(TrafficListScreen);

export default TrafficListScreen;


